.main-header{
  position: relative;
  background: url("../../img/headerBackNN.png") no-repeat center;
  background-size: 1220px 1000px;
  padding-bottom: 920px;
  &>img{
    width: 440px;
    position: absolute;
    left: calc(50% - 220px);
    top: 220px;
  }
}

.container_main-mav{
  width: 100%;
  background: rgba(229,230,230, 0.7);
  padding-top: 20px;
  padding-bottom: 20px;
}

.main-nav_list{
  width: 1210px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  padding: 0;
  font-family: Dosis-Regular, Arial, sans-serif;
  font-size: 17px;
  list-style-type: none;
}

.main-nav_item a{
  color: #606060;
  text-decoration: none;
}